import { Component, OnInit, ViewEncapsulation, ViewChild, NgZone, ElementRef, Input, ChangeDetectorRef } from '@angular/core';
import { FormControl, FormsModule, ReactiveFormsModule, FormBuilder } from "@angular/forms";
import { Location } from '@angular/common';
import { AppState } from '../app.state';
import { Observable } from 'rxjs/Rx';
import { DirectionsMapDirective } from './google-map.directive';
import { UrlUtility } from './pages.httpurl'
import { MapService } from './pages.mapservice'
import { Subscription } from 'rxjs/Subscription';
import { DirectionsRenderer } from '@ngui/map';

@Component({
    selector: 'az-pages',
    encapsulation: ViewEncapsulation.None,
    templateUrl: './pages.component.html',
    styleUrls: ['./pages.component.scss'],
    providers: [AppState, FormBuilder, MapService]
})

export class PagesComponent implements OnInit {
    @ViewChild(DirectionsRenderer) directionsRendererDirective: DirectionsRenderer;
    public gpsJson: any = {};
    public lat: number = 18.571635;
    public lng: number = 73.7707283;
    public zoom: number = 16;
    public latlng = "18.571635, 73.7707283";
    public position: any = [];
    public deviceId: number = 0;
    public mapPath: any = [];
    public vehicleJson: any = [];
    directionsRenderer: google.maps.DirectionsRenderer;
    directionsResult: google.maps.DirectionsResult;
    direction: any = {
        origin: '',
        destination: '',
        travelMode: 'DRIVING'
    };
    public marker: any = {
        display: true,
        lat: 18.571635,
        lng: 73.7707283,
        veh: 'MH 02 AA 1111',
        driver: '',
        speed: '40',
        run: ''
    };



    public isMenuCollapsed: boolean = false;
    constructor(private _state: AppState, private _location: Location, private mapService: MapService,
        private cdr: ChangeDetectorRef) {

        this._state.subscribe('menu.isCollapsed', (isCollapsed) => {
            this.isMenuCollapsed = isCollapsed;
        });

        Observable.interval(1000)
            .subscribe((x) => {
                this.mapPath = this.mapService.getMapPath();
                this.direction = this.mapService.getdirectionsRenderer();
                if (this.mapPath != "") {
                    this.latlng = this.mapPath[0];
                }
                this.setMapMarkerPosition();
            });

        Observable.interval(15000)
            .subscribe((x) => {
                if (this.deviceId != 0) {
                    this.getdeviceGpsData();
                }
            });
    }

    ngOnInit() {
        this.getCurrentPageName();
        this.directionsRendererDirective['initialized$'].subscribe(directionsRenderer => {
            this.directionsRenderer = directionsRenderer;
        });
        this.getAllVehicleData();
    }

    showDirection() {
        this.directionsRendererDirective['showDirections'](this.direction);
    }

    directionsChanged() {
        this.directionsResult = this.directionsRenderer.getDirections();
        this.cdr.detectChanges();
    }

    public getdeviceGpsData() {
        let values = {};
        values["device_id"] = this.deviceId;
        this._state.getGps(values).subscribe(
            data => {
                this.gpsJson = data;
                this.lat = Number(this.gpsJson.Latitude);
                this.lng = Number(this.gpsJson.Longitude);
                let localval = Number(this.gpsJson.Latitude) + "," + Number(this.gpsJson.Longitude);
                this.position.forEach((item, index) => {
                    if (item.K == this.gpsJson.Device_ID) {
                        this.position[index].P = localval;
                    }
                });

                if (this.mapPath == "") {
                    this.latlng = localval;
                }
            }
        );
    }

    public setMapMarkerPosition() {
        let pos = JSON.stringify(this.mapService.getPosition());
        if (pos != '{}') {
            pos = JSON.parse(pos);
            this.deviceId = Number(pos["DeviceId"]);
            this.latlng = pos["LatLong"];
            this.zoom = 16;
        }
    }
    
    public getAllVehicleData() {
        this._state.getVehicle().subscribe(
            data => {
                this.vehicleJson = data["fleet_status"];
                this.position = [];
                this.vehicleJson.forEach((item, index) => {
                    this.position.push({ 'K': item.device_imei, 'P': item.latitude + ',' + item.longitude });
                });                
            }
        );
    }
    
    public displayMarkerInfo(event, device_key) {
        let marker = event.target;
        this.marker.lat = marker.getPosition().lat().toFixed(6);
        this.marker.lng = marker.getPosition().lng().toFixed(6);

        this.vehicleJson.forEach((item, index) => {
            if (item.device_imei == device_key) {
                this.marker.veh = item.vehicle;
                this.marker.driver = item.first_name + ' ' + item.last_name;
                this.marker.speed = item.speed;
                if (item.status == 0)
                    this.marker.run = 'Idle';
                else
                    this.marker.run = 'Running';
            }
        });
        marker.nguiMapComponent.openInfoWindow('iw', marker);
    }


    public getCurrentPageName(): void {
        let url = this._location.path();
        let hash = (window.location.hash) ? '#' : '';
        setTimeout(function() {
            let subMenu = jQuery('a[href="' + hash + url + '"]').closest("li").closest("ul");
            window.scrollTo(0, 0);
            subMenu.closest("li").addClass("sidebar-item-expanded");
            subMenu.slideDown(250);
        });
    }

    public hideMenu(): void {
        this._state.notifyDataChanged('menu.isCollapsed', true);
    }

    public ngAfterViewInit(): void {
        document.getElementById('preloader').style['display'] = 'none';
    }


}



