import { GoogleMapsAPIWrapper }  from '@agm/core';
import { Directive, Component, Input, Output } from '@angular/core';

declare var google: any;

@Directive({
    selector: 'sebm-google-map-directions'
})

export class DirectionsMapDirective {
    @Input() origin: any;
    @Input() destination: any;
    @Input() originPlaceId: any;
    @Input() destinationPlaceId: any;
    @Input() waypoints: any;
    @Input() directionsDisplay: any;
    @Input() estimatedTime: any;
    @Input() estimatedDistance: any;

    constructor(private gmapsApi: GoogleMapsAPIWrapper) {

    }
    ngOnInit() {
        this.getDirectionHistory();
    }

    public getDirectionHistory() {
        this.gmapsApi.getNativeMap().then(map => {
            var start = new google.maps.LatLng(0, 0);
            var end = new google.maps.LatLng(0, 0);

            start = new google.maps.LatLng(18.694004, 73.110291);
            end = new google.maps.LatLng(18.72082, 73.7718833);

            var directionsService = new google.maps.DirectionsService;
            var directionsDisplay = new google.maps.DirectionsRenderer;
            directionsDisplay.setMap(map);
            directionsDisplay.setOptions({
                polylineOptions: {
                    strokeWeight: 8,
                    strokeOpacity: 0.7,
                    strokeColor: '#0011cc'
                }
            });
            var request = {
                origin: start,
                destination: end,
                travelMode: google.maps.TravelMode.DRIVING
            };
            var directionsService = new google.maps.DirectionsService();
            directionsService.route(request, function(response, status) {
                if (status == google.maps.DirectionsStatus.OK) {
                    directionsDisplay.setDirections(response);
                }
            });
        });
    }

}


